import Logo from '../assets/images/logo.png'
import Bgck from '../assets/images/home-dark.png'
import './main.scss'
import './response.scss'

const Header = ()=>{
    return(
       <div className="site-wrapper">
          
            <div className="for-bg"
            style={
                {
                    backgroundImage: `url(${Bgck})`
                }
            }>
            <div className="header-wrapper">
            <div className="container">
                <div className="site-logo">
                        <img src={Logo} alt="" />
                </div>
                <div className="navbar">
                    <ul>
                        <li>
                            <a href="#">Home</a>
                        </li>
                        
                        <li>
                            <a href="#full-info">About</a>
                        </li>
                        
                        <li>
                            <a href="#">Portofilio</a>
                        </li>
                        
                        <li>
                            <a href="#contact">Contacts</a>
                        </li>
                    </ul>
                </div>
            </div>
            </div>

                <section className="info-site">
                    <div className="info-inner container">
                        <h2 className="dev-name">Hello, I'm <span>J</span>avohir <span>S</span>hokirov</h2>

                        <h1 className="field">I'm a <span className="type"></span></h1>
                       
                        
                        <p className="info-i">
                             I'm a Web Developer with extensive experience for over 3 years. My <br /> expertise is to create and design Websites, Apps and more...
                        </p>
                        <a href="#" className="work grey">My Work</a>
                        <a href="#" className="work blue">Hire me</a>
                <a href="#full-info" className="about-me">About me</a>
                    </div>
                </section>

            </div>

            
       </div>
    )
}

export default Header